<template>
    <div class="p-20">
        <div class="filter-container" style="margin-bottom: 20px">
            <el-input class="filter-item" style="width: 300px;" placeholder="请输入会员UID、手机号码"
                      @keyup.native.enter="handleFilter"
                      v-model="listQuery.nameUidNum">
            </el-input>

            <el-button class="filter-item" @click="handleFilter" size="small" style="margin-left: 30px">查询</el-button>
            <el-button type="primary" class="filter-item" @click="handleDeleteEdit()" size="small" style="margin-left: 30px">
                审核
            </el-button>
        </div>
        <div>
            <el-table
                    ref="singleTable"
                    :data="tableData"
                    v-loading="listLoading"
                    :highlight-current-row="true"
                    @row-click="handleCurrentChange"
                    element-loading-text="加载中......."
                    style="width: 100%;">
                <el-table-column
                        v-for="item in tableHeader"
                        :key="item.prop"
                        :label="item.label"
                        :prop="item.prop"
                        :width="item.width"
                        :min-width="item.minWidth"
                        :align="item.align"
                        :formatter="item.formatter"
                        show-overflow-tooltip>
                </el-table-column>
                <!--<el-table-column
                        fixed="right"
                        label="操作"
                        width="170">
                    <template scope="scope" class="filter-operation">
                        <ul style="list-style: none;padding: 0px;margin: 0px">
                            <li style="float:left;width: 50px">
                                <el-button
                                        type="text"
                                        @click="handleDeleteEdit(scope.row.userId)">审核
                                </el-button>
                            </li>
                        </ul>
                    </template>
                </el-table-column>-->
            </el-table>
            <div class="pagination">
                <el-pagination @size-change="userPageSize" @current-change="userPage"
                               :current-page="listQuery.page"
                               :page-sizes="[15,30,45,60]" :page-size="listQuery.pageSize"
                               layout="total, sizes, prev, pager, next, jumper" :total="total">
                </el-pagination>
            </div>
        </div>
        <el-dialog title="身份认证信息" :visible.sync="dialogFormVisible" :close-on-click-modal="false">
            <el-form :model="temp">
                <el-form-item label="证件认证状态" :label-width="formLabelWidth">
                    <template>
                        <div v-if="temp.identityValidateState == 0">
                            未提交
                        </div>
                        <div v-else-if="temp.identityValidateState == 1">
                            待审核
                        </div>
                        <div v-else-if="temp.identityValidateState  == 3">
                            已认证
                        </div>
                        <div v-else-if="temp.identityValidateState  == 4">
                            不通过
                        </div>
                    </template>
                </el-form-item>
                <el-form-item label="姓名" :label-width="formLabelWidth">
                    <el-input v-model="temp.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="国家/地区" :label-width="formLabelWidth">
                    <el-select class="filter-item" v-model="temp.identityArea"
                               @change="handleChange">
                        <el-option v-for="item in citybos"
                                   :key="item.countryCode"
                                   :label="item.cityName"
                                   :value="item.cityId">
                        </el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="证件类型" :label-width="formLabelWidth">
                    <el-select class="filter-item" v-model="temp.identityType">
                        <el-option v-for="item in identityType"
                                   :key="item.id"
                                   :label="item.name"
                                   :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="证件号码" :label-width="formLabelWidth">
                    <el-input v-model="temp.idCard"></el-input>
                </el-form-item>
                <el-form-item label="身份证正面" :label-width="formLabelWidth">
                    <el-popover
                        ref="popover1"
                        placement="right-start"
                        title=""
                        width="500"
                        trigger="click"
                        content="">
                        <img :src="temp.reversePathImg" alt="" class="img-big">
                    </el-popover>
                    <img v-popover:popover1 v-bind:src="temp.reversePathImg" class="images">
                </el-form-item>
                <el-form-item label="身份证反面" :label-width="formLabelWidth">
                    <el-popover
                        ref="popover2"
                        placement="right-start"
                        title=""
                        width="500"
                        trigger="click"
                        content="">
                        <img :src="temp.reversePathImg" alt="" class="img-big">
                    </el-popover>
                    <img v-popover:popover2 v-bind:src="temp.reversePathImg" class="images">
                </el-form-item>
                <el-form-item label="手持身份证" :label-width="formLabelWidth">
                    <el-popover
                        ref="popover3"
                        placement="right-start"
                        title=""
                        width="500"
                        trigger="click"
                        content="">
                        <img :src="temp.handPath" alt="" class="img-big">
                    </el-popover>
                    <img v-popover:popover3 v-bind:src="temp.handPath" class="images">
                </el-form-item>
                <el-form-item label="审核结果" :label-width="formLabelWidth">
                    <el-radio v-model="temp.photoidValidate" :label="3">通过审核</el-radio>
                    <el-radio v-model="temp.photoidValidate" :label="4">驳回</el-radio>
                </el-form-item>
                <div v-if="temp.photoidValidate===4">
                    <el-form-item label="驳回原因" :label-width="formLabelWidth">
                        <el-input v-model="temp.checkRemark" auto-complete="off" value="form.checkRemark" type="textarea"
                        ></el-input>
                    </el-form-item>
                </div>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible=false">取 消</el-button>
                <el-button type="primary" @click="updateUser">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    //import fetch from '@/utils/fetch'
    import DCTP from '@/utils/dctp';
    import {fetchList, alterUser, disableUser, selectUser, getCity} from '@/api/management'

    const calendarTypeOptions = [
        {key: '', display_name: '全部状态'},
        {key: '1', display_name: '启用'},
        {key: '2', display_name: '禁用'},
    ];
    const logintypeOptions = [
        {key: 0, display_name: '全部状态'},
        {key: 1, display_name: '手机注册'},
        {key: 2, display_name: '邮箱注册'},
        {key: 3, display_name: 'QQ注册'},
        {key: 4, display_name: '微信注册'},
    ];
    const importanceOptions = [
        {key: '', display_name: '全部状态'},
        {key: 0, display_name: '未提交'},
        {key: 1, display_name: '待审核'},
        {key: 3, display_name: '已认证'},
        {key: 4, display_name: '不通过'},
    ];
    export default {
        data() {
            return {
                logintypeOptions,
                //总条数
                total: null,
                //返回来列表的数据
                tableData: [],
                citybos: [],
                listLoading: true,
                //查看身份新增框自动隐藏
                dialogFormVisible: false,
                identityType: null,
                //查看身份信息双向绑定数据
                temp: {
                    //uid
                    userId: '',
                    //固定审核状态
                    identityValidateState: '',
                    //真实姓名
                    name: '',
                    //证件地区
                    identityArea: '',
                    //证件类型
                    identityType: '',
                    //身份证号码
                    idCard: '',
                    //备注
                    checkRemark: '',
                    //审核值
                    identityValidate: '',
                },
                //单个查询
                selectUserId: {
                    userId: ''
                },
                //查看身份证信息的左边宽度
                formLabelWidth: '120px',
                //点击获取选择的值
                currentRow: null,
                //查询条件对象
                listQuery: {
                    nameUidNum: '',
                    accountStatus: 0,
                    identityValidate: 3,
                    photoidValidate: 1,
                    videoValidate: '',
                    pageSize: 15,
                    page: 1
                },
                msg: '',
                tableHeader: [
                    {
                        label: "UID",
                        prop: "userId",
                        width: 65
                    }, {
                        label: "手机号码",
                        prop: "phoneNum",
                        width: 130
                    }, {
                        label: "邮箱",
                        prop: "email",
                        width: 160
                    }, {
                        label: "姓名",
                        prop: "name",
                        minWidth: 120,
                    }, {
                        label: "证件类型",
                        prop: "identityType",
                        minWidth: 95,
                        align: 'center',
                        formatter(row, column, value) {
                            return value === 0 ? '身份证' : (value === 2 ? '护照' : '未知');
                        }
                    }, {
                        label: "国籍",
                        // prop: "cndict",
                        minWidth: 120,
                        formatter(row, column, value) {
                            return row.cndict === null ? "未提交" : row.cndict + "(" + row.endict + ")";
                        }
                    }, {
                        label: "证件号码",
                        prop: "idCard",
                        minWidth: 95,
                        align: 'center',
                    }, {
                        label: "实名认证",
                        prop: "identityValidate",
                        width: 110,
                        align: 'center',
                        formatter(row, column, value) {
                            return row.videoValidate == 3 ? "C3认证" : (row.photoidValidate == 3 ? "C2认证" : (row.identityType == 3 ? "C1认证" : "未认证"))
                        }
                    }, {
                        label: "状态",
                        prop: "accountStatus",
                        width: 80,
                        align: 'center',
                        formatter(row, column, value) {
                            return value === 1 ? '正常' : (value === 2 ? '禁用' : '未知');
                        }
                    }, {
                        label: "登录IP",
                        prop: "0",

                    }, {
                        label: "申请时间",
                        prop: "photoidTime",
                        width: 170,
                        formatter(row, column, value) {
                            return DCTP.utils.parseDateTimeStr(value)
                        }
                    }, {
                        label: "注册时间",
                        prop: "createTime",
                        width: 170,
                        formatter(row, column, value) {
                            return DCTP.utils.parseDateTimeStr(value)
                        }
                    }, {
                        label: "最后更新时间",
                        prop: "updateTime",
                        width: 170,
                        formatter(row, column, value) {
                            return DCTP.utils.parseDateTimeStr(value)
                        }
                    }
                ]
            }

        }
        ,
        computed: {
            tableDataLength() {
                return this.tableData.length
            }
        }
        ,
        methods: {
            getList() {
                this.listLoading = true;
                fetchList(this.listQuery).then(response => {
                    this.tableData = response.data.userList;
                    this.total = response.data.total;
                    this.listLoading = false;
                })
            },
            /**
             * 条件筛选查询对象
             * @returns {Promise}
             */
            handleFilter() {
                this.getList();
            },
            //获取当前页数
            userPage(val) {
                this.listQuery.page = val;
                this.getList();
            },
            //获取每页多少条
            userPageSize(val) {
                this.listQuery.pageSize = val;
                this.getList();
            },
            /**
             *获取选中会员的所有信息
             */
            updateUser() {
                if (this.temp.photoidValidate === 3) {
                    this.temp.checkRemark = '';
                }
                alterUser(this.temp).then(response => {
                    if (response.result) {
                        this.dialogFormVisible = false;
                        this.msg = "操作成功!";
                        this.$message({
                            type: 'success',
                            message: this.msg
                        });
                        this.currentRow = null;
                        this.getList();
                    } else {
                        this.msg = response.msg;
                        this.$message({
                            type: 'error',
                            message: this.msg
                        });
                    }

                })
            },

            /**
             *修改身份信息方法
             * @returns {Promise}
             */

            handleChange(val) {
                if (val === 1) {
                    this.temp.identityType = 0;
                } else {
                    this.temp.identityType = 2;
                }
                if (val === 1) {
                    this.identityType = [{id: 0, name: '身份证'}];
                } else {
                    this.identityType = [{id: 2, name: '护照'}];
                }
            },
            validate() {
                if (!this.temp.name) {
                    this.$message.warning('请输入姓名');
                    return false;
                }
                return true;
            },
            handleDeleteEdit() {
                if (!this.currentRow) {
                    this.getMessage('请选择用户',false);
                    return;
                }
                let userId = this.currentRow.userId;
                this.dialogFormVisible = true;
                this.listLoad = true;
                this.selectUserId.userId = userId;
                selectUser(this.selectUserId).then(response => {
                    this.temp = response.data;
                    this.temp.identityValidateState = response.data.identityValidate;
                    if (response.data.identityValidate !== 3 && response.data.identityValidate !== 4) {
                        this.temp.identityValidate = 3;
                    }
                    if (response.data.photoidValidate !== 3 && response.data.photoidValidate !== 4) {
                        this.temp.photoidValidate = 3;
                    }
                    this.listLoad = false;
                });

            },
            getCityList() {
                getCity().then(result => {
                    this.citybos = result.data.citybos;
                })
            },
            handleCurrentChange(val) {
                console.log(val);
                this.currentRow = val;
            }
        }
        ,
        created() {
            this.getList();
            this.getCityList();
        }
    }
</script>

<style>
    .el-dialog--small {
        width: 30%;
    }
    .images{
        width: 200px;
        height: 150px;
    }
    .img-big {
        width: 100%
    }

</style>



