import fetch from '@/utils/fetch'

export function fetchList(query) {
    return fetch({
        url: 'admin/conditionuser',
        method: 'get',
        params: query
    })
}


export function alterUser(query) {
    return fetch({
        url: 'admin/updateuser',
        method: 'put',
        data: query
    })
}

export function disableUser(query) {
    return fetch({
        url: 'admin/enabledisable',
        method: 'put',
        data: query
    })
}

export function verifiedUser(query) {
    return fetch({
        url: 'admin/checkuser',
        method: 'put',
        data: query
    })
}

export function selectUser(query) {
    return fetch({
        url: 'admin/selectuser',
        method: 'get',
        params: query
    })
}


export function getCity() {
    return fetch({
        url: 'admin/getCity',
        method: 'get'
    })
}

export function resetUpdate(data) {
    return fetch({
        url: 'admin/resetOrUpdate',
        method: 'put',
        data: data
    })
}

export function unlockLoginPwd(data) {
    return fetch({
        url: 'admin/unlockLoginPwd/' + data,
        method: 'put',
    })
}


